import React, {Component} from 'react';
import echarts from "echarts";

class Skills extends Component {
    constructor(props) {
        super(props);
        this.main = React.createRef();
    }

    render() {
        return (

            <div style={{width: 600, height: 400}} ref={this.main}></div>
        );
    }

    componentDidMount() {
        let myChart = echarts.init(this.main.current);
        myChart.setOption({
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b}: {c} ({d}%)'
            },
            legend: {
                orient: 'vertical',
                left: 10,
                data: ['web开发', 'Java', 'C#', 'unity3D', 'Python']
            },
            series: [
                {
                    name: '访问来源',
                    type: 'pie',
                    radius: ['50%', '70%'],
                    avoidLabelOverlap: false,
                    label: {
                        show: false,
                        position: 'center'
                    },
                    emphasis: {
                        label: {
                            show: true,
                            fontSize: '30',
                            fontWeight: 'bold'
                        }
                    },
                    labelLine: {
                        show: false
                    },
                    data: [
                        {value: 200, name: 'web开发'},
                        {value: 70, name: 'Java'},
                        {value: 70, name: 'C#'},
                        {value: 70, name: 'unity3D'},
                        {value: 30, name: 'Python'},
                    ]
                }
            ]
        });
    }
}

export default Skills;