import React, {Component} from 'react';
import Cut from "../../Cut/Cut";
import styles from "./Project.module.scss";
import {Menu, Row, Col} from 'antd';

class Project extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chooseKey: "1",
            projectIsShow: true
        };
    }

    render() {
        const {chooseKey} = this.state;
        return (
            <div id={styles.projects}>
                <Cut _showAndHidden={() => this._projectShowAndHidden()} text="项目经历"/>
                <Row className={(this.state.projectIsShow ? "show " : "hidden ") + "myAnimate " + styles.project}>
                    <Col span={5}>
                        <Menu
                            onClick={(e) => this.handleClick(e)}
                            style={{width: 200}}
                            defaultSelectedKeys={['1']}
                            mode="inline"
                        >
                            <Menu.ItemGroup key="g1" title="项目列表">
                                <Menu.Item key="1">Web前端开发</Menu.Item>
                                <Menu.Item key="2">后台管理系统</Menu.Item>
                                <Menu.Item key="3">主页后端</Menu.Item>
                                <Menu.Item key="4">微信小程序</Menu.Item>
                                <Menu.Item key="5">萝卜TD</Menu.Item>
                            </Menu.ItemGroup>
                        </Menu>
                    </Col>
                    <Col span={19}>
                        <ul>
                            {/*个人主页*/}
                            {(chooseKey === "1") ? <li className={(chooseKey === "1") ? "animated fadeIn" : ""}>
                                <h1>个人主页</h1>
                                <p>2019.11 - 至今</p>
                                <ol>
                                    <li>
                                        <h3>1、react：欢迎界面使用react框架搭建，使用Animate.css做一些过渡动画，使用AI软件制作svg。同时也对手机屏幕进行了适配。</h3>
                                    </li>
                                    <li>
                                        <h3>2、vue：使用element ui框架进行制作个人博客部分，通过axios进行提交数据和接收数据，需要复杂交互的界面使用vuex进行搭建，简单的交互界面直接使用了vue中的组件传递数据。</h3>
                                    </li>
                                    <li>
                                        <h3>3、个人主页地址： <a href="mikumirai.github.io">qflyb.github.io</a></h3>
                                    </li>
                                </ol>
                            </li> : ""}
                            {/*后台管理系统*/}
                            {(chooseKey === "2") ? <li className={(chooseKey === "2") ? "animated fadeIn" : ""}>
                                <h1>后台管理系统</h1>
                                <p>2019.11 - 至今</p>
                                <h3>1、使用react框架进行搭建，同时使用了antd UI组件。</h3>
                                <h3>2、对个人主页界面上的数据进行删除操作，使用react-redux和redux-saga进行数据管理。</h3>
                                <h3>3、使用axios通过接口对数据库中的数据进行增删改查。</h3>
                            </li> : ""}
                            {/*主页后端*/}
                            {(chooseKey === "3") ? <li className={(chooseKey === "2") ? "animated fadeIn" : ""}>
                                <h1>主页后端</h1>
                                <p>2019.11 - 至今</p>
                                <h3>1、使用SpringBoot进行搭建，负责处理个人主页上的图片，评论数据。</h3>
                                <h3>2、使用MySQL搭建数据库，并且使用jpa和mybatis操作数据库。</h3>
                            </li> : ""}
                            {/*微信小程序*/}
                            {(chooseKey === "4") ? <li className={(chooseKey === "3") ? "animated fadeIn" : ""}>
                                <h1>星座和亲戚计算器</h1>
                                <p>停止更新</p>
                                <img style={{margin: "10px 50px"}} height="300px"
                                     src={require("../../../../images/home/show/2.gif")} alt=""/>
                                <h3>1、该微信小程序可以根据出生年月日计算出对应的星座，并且根据微信提供的后端数据库取出对应的星座数据。</h3>
                                <h3>2、逢年过节走亲访友时总会遇到不知道怎么称呼对方的尴尬情况，而该亲戚关系计算器可以根据你输入的亲戚关系，自动计算出你称呼他，他称呼你的对象，同时判定无法输入同性配偶。</h3>
                            </li> : ""}
                            {/*unity游戏*/}
                            {(chooseKey === "5") ? <li className={(chooseKey === "4") ? "animated fadeIn" : ""}>
                                <h1>萝卜TD</h1>
                                <p>2018.07 - 2018.08</p>
                                <h3>1、通根据保卫萝卜进行复刻，并在其上增加了地图编辑器，可以实现自定义地图、自定义放塔点、自定义怪物路径、自定义地图背景等功能。</h3>
                                <Row align="middle">
                                    <Col span={10}>
                                        <img height="250px" src={require("../../../../images/home/show/3.gif")} alt=""/>
                                    </Col>
                                    <Col span={14}>
                                        <h3>2、核心算法：地图信息以及放塔点信息，怪物路径信息都通过 CSV进行导入数据。导入后将屏幕分为 8*12
                                            个格子，然后再通过计算出每个格子中心点的坐标，让怪物沿着已经设定好的路径点通过 Translat
                                            进行移动，如果当怪物离某个路径点的距离小于某值，则让它朝着下一个点进行移动，如果已经达到终点，则将怪物的渲染关闭。搜寻怪物逻辑即将所有怪物放在一个数组中，先遍历这个数组，然后判断怪物是否在塔的攻击范围中，如果在塔的攻击范围中，则将炮塔的目标设置为当前怪物。</h3>
                                    </Col>
                                </Row>
                            </li> : ""}
                        </ul>
                    </Col>
                </Row>
            </div>
        );
    }

    handleClick(e) {
        let chooseKey = this.state.chooseKey;
        chooseKey = e.key;
        this.setState({chooseKey});
    };

    _projectShowAndHidden() {
        let {projectIsShow} = this.state;
        projectIsShow = !projectIsShow;
        this.setState({
            projectIsShow
        });
    }
}

export default Project;
