import React, {Component} from 'react';
import styles from "./Header.module.scss";
import Cut from "../Cut/Cut";
class Header extends Component {
    constructor(poros) {
        super(poros);
        this.state = {
            introductionIsShow: true
        };
    }

    render() {
        const {introductionIsShow} = this.state;
        return (
            <header id={styles.header}>
                <Cut _showAndHidden={() => this._introductionShowAndHidden()} text="个人介绍"/>
                <div className={(introductionIsShow ? "show " : "hidden ") + "myAnimate"}>
                    <embed className={styles.svg} src={require("../../../svg/myintroduction.svg")} height="350"/>
                </div>
            </header>
        );
    }

    _introductionShowAndHidden() {
        let {introductionIsShow} = this.state;
        introductionIsShow = !introductionIsShow;
        this.setState({
            introductionIsShow
        });
    }
}

export default Header;