import React, {Component} from 'react';
import styles from "./Cut.module.scss";

class Cut extends Component {
    render() {
        const {text, _showAndHidden} = this.props;
        return (
            <div id={styles.cut} onClick={() => _showAndHidden()}>
                <h2>{text}</h2>
            </div>
        );
    }
}

export default Cut;