import React, {Component} from 'react';
import styles from "./Main.module.scss";
import Cut from "../Cut/Cut";
import Work from "./Work/Work";
import Project from "./Project/Project";
import {Popover, Button} from 'antd';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            backgroundIsShow: true,
            projectIsShow: true
        };
    }

    render() {
        const {backgroundIsShow} = this.state;
        return (
            <main id={styles.main}>
                <div className={styles.background}>
                    <Cut _showAndHidden={() => this._backgroundShowAndHidden()} text="教育背景"/>
                    <ul className={(backgroundIsShow ? "show " : "hidden ") + "myAnimate"}>
                        <li>
                            <Popover placement="right" content="2012.09-2016.12 建筑经济管理 本科" trigger="hover">
                                <Button>西南科技大学</Button>
                            </Popover>
                        </li>
                        <li>
                            <Popover placement="right" content="2012.09-2015.06 工程造价 专科" trigger="hover">
                                <Button>四川城市职业学院</Button>
                            </Popover>
                        </li>
                    </ul>
                </div>
                <Work/>
                <Project/>
            </main>
        );
    }

    _backgroundShowAndHidden() {
        let {backgroundIsShow} = this.state;
        backgroundIsShow = !backgroundIsShow;
        this.setState({
            backgroundIsShow
        });
    }
}

export default Main;