import React, {Component} from 'react';
import styles from "./Footer.module.scss";
import Cut from "../Cut/Cut";
import Skills from "./Skills/Skills";
class Footer extends Component {
    constructor(props) {
        super(props);
        this.state={
            skillIsShow: true,
            hobbyIsShow: true,
            evaluate:true
        }
    }
    render() {
        const {skillIsShow,hobbyIsShow,evaluate} = this.state;
        return (
            <footer id={styles.footer}>
                <div className={styles.skill}>
                    <Cut _showAndHidden={() => this._skillShowAndHidden()} text="职业技能"/>
                    <div  className={(skillIsShow ? "show " : "hidden ") + "myAnimate"}>

                        <Skills/>
                    </div>
                </div>
                <div className={styles.hobby}>
                    <Cut _showAndHidden={() => this._hobbyShowAndHidden()} text="兴趣爱好"/>
                    <h3 className={(hobbyIsShow ? "show " : "hidden ") + "myAnimate"}>
                        编程 阅读
                    </h3>
                    <Cut _showAndHidden={() => this._evaluateShowAndHidden()} text="自我评价"/>
                    <div className={(evaluate ? "show " : "hidden ") + "myAnimate"}>
                        <h3>乐于学习，对新的事物有较强的接收能力，自学了HTML，CSS，JavaScript，Java，Python等一些列编程相关知识，追求效率，不拖沓，责任心强。</h3>
                    </div>

                </div>
            </footer>
        );
    }
    _skillShowAndHidden() {
        let {skillIsShow} = this.state;
        skillIsShow = !skillIsShow;
        this.setState({
            skillIsShow
        });
    }

    _hobbyShowAndHidden() {
        let {hobbyIsShow} = this.state;
        hobbyIsShow = !hobbyIsShow;
        this.setState({
            hobbyIsShow
        });
    }
    _evaluateShowAndHidden(){
        let {evaluate} = this.state;
        evaluate = !evaluate;
        this.setState({
            evaluate
        });
    }

}

export default Footer;